import App from "./ConfiguraComfort.svelte";
const initConfig = {
    mainTitle: "Customize your Accora Configura Comfort",
    bookADemoHref: "https://us.accora.care/book-a-demo",
};
export const ConfiguraComfort = (targetId, config, hubspotFormConfig) => {
    const app = new App({
        target: document.getElementById(targetId),
        props: {
            config: Object.assign(Object.assign(Object.assign({}, initConfig), config), { hubspotFormConfig }),
        },
    });
    return app;
};
window.ConfiguraComfort = ConfiguraComfort;
export default ConfiguraComfort;
